.flWrap{
  min-height: 60vh;
  width: 45%;
}

.flWrap ul {
  padding: 50px;
}

.flWrap ul li {
  padding-bottom: 10px;
  padding-top: 10px;
}

.flWrap a {
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  color: #178077;
  text-transform: uppercase;
  padding-bottom: 10px;
  padding-top: 10px;
}

.flWrap a:hover {
  color: #fff;
  background: #178077;
}

@media (max-width: 663px) {
  .flWrap{
    width: 99%;
  }
}